<script>
// let tokens = ['021Abc9Abc1']

let tokens = ['021Abc9000', '021Abc9Abc1', '021ABC9000', '021#bc9000']

for (let value of tokens) {
  let mark = 0

  if (value.match(/\d/)) {
    mark++
  }
  if (value.match(/[a-z]/)) {
    mark++
  }
  if (value.match(/[A-Z]/)) {
    mark++
  }
  if (value.match(/[^[a-zA-Z0-9]]/)) {
    mark++
  }

  if (mark < 3) {
    console.log('NG')
    continue
  } else if (!check(value)) {
    console.log('NG')
    continue
  }
  console.log('OK')
}
function check(arr) {
  // let arr = arrr.join('')
  for (let i = 0; i < arr.length - 3; i++) {
    for (let j = arr.length - 3; j > i; j--) {
      if (arr.slice(i, i + 3) === arr.slice(j, j + 3)) {
        // console.log(arr.slice(i, i + 3), arr.slice(j, j + 3))
        return false
        // console.log('NG')
      }
    }
  }
  return true
}
let str = '@'
console.log(str, str.match(/[^a-zA-Z0-9]/))

import { upload } from '../../../utils/upload.js'
export default {
